import { NgModule, ErrorHandler } from '@angular/core';
import { BrowserModule } from '@angular/platform-browser';
import { IonicApp, IonicModule, IonicErrorHandler } from 'ionic-angular';
import { MyApp } from './app.component';

import { AboutPage } from '../pages/about/about';
import { ContactPage } from '../pages/contact/contact';
import { HomePage } from '../pages/home/home';
import { TabsPage } from '../pages/tabs/tabs';

import { StatusBar } from '@ionic-native/status-bar';
import { SplashScreen } from '@ionic-native/splash-screen';

import { MainMusicPage } from "../pages/main-music/main-music";
import { ExploreMusicPage } from "../pages/explore-music/explore-music";
import { FriendsPage } from "../pages/friends/friends";

import { SpotifyPage } from "../pages/spotify/spotify";
import { InternetArchivePage } from "../pages/internet-archive/internet-archive";


@NgModule({
  declarations: [
    MyApp,
    AboutPage,
    ContactPage,
    HomePage,
    TabsPage,
    MainMusicPage,
    ExploreMusicPage,
    FriendsPage,
    SpotifyPage,
    InternetArchivePage
  ],
  imports: [
    BrowserModule,
    IonicModule.forRoot(MyApp)
  ],
  bootstrap: [IonicApp],
  entryComponents: [
    MyApp,
    AboutPage,
    ContactPage,
    HomePage,
    TabsPage,
    MainMusicPage,
    ExploreMusicPage,
    FriendsPage,
    SpotifyPage,
    InternetArchivePage
  ],
  providers: [
    StatusBar,
    SplashScreen,
    {provide: ErrorHandler, useClass: IonicErrorHandler}
  ]
})
export class AppModule {}



